import { Link, useNavigate } from "react-router-dom";
import React, { useEffect, useState } from "react";
import { Radio, Space } from "antd";
const FullData = () => {
  const [data, setData] = useState({
    existingPatient: "",
    firstName: "",
    lastName: "",
    DOB: "",
    gender: "",
    email: "",
    phoneNumber: "",
    address1: "",
    address2: "",
    city: "",
    state: "",
    country: "",
    postalCode: "",
  });
  const [updatedData, setUpdatedData] = useState({});

  const navigation = useNavigate();
  useEffect(() => {
    loadData();
  }, []);
  const loadData = () => {
    const storedData = localStorage.getItem("appointment-data");
    if (storedData) {
      setUpdatedData(JSON.parse(storedData));
    }
  };

  const handleUpdateData = (values) => {
    // Update data with additional fields
    const newData = { ...updatedData, ...values };

    // Store updated data in localStorage
    localStorage.setItem("appointment-data", JSON.stringify(newData));

    // Optionally, you can update state to reflect the changes
    setUpdatedData(newData);
    navigation("/confrim-appoint");
  };

  return (
    <>
      <div className="header_flex">
        <div className="d-flex items_cnt">
          <img src="/images/arrow-left-light 1.svg" alt="" />
          <Link to="/PatientInfo">
            <div className="backbtn">Back</div>
          </Link>
        </div>
        <Link to="/">
          <img src="/images/weblogo.svg" alt="" />
        </Link>
        <div></div>
      </div>
      <div className="slot">
        <h2 className="">Patient details</h2>
      </div>
      <div className="solt_flex">
        <div>
          <div className="calender">
            <div className="mb-4" style={{ fontFamily: "kanit-bold" }}>
              Are you an existing patient ?
            </div>
            <Radio.Group
              onChange={(e) => {
                setData({ ...data, existingPatient: e.target.value });
              }}
              name="existingPatient"
            >
              <Space direction="vertical">
                <Radio value="yes">YES</Radio>
                <Radio value="no">NO</Radio>
              </Space>
            </Radio.Group>
          </div>
        </div>

        <div className="bottom_line"></div>
        <div className="slect_appoint">
          <div className="mb-3" style={{ fontFamily: "kanit-bold" }}>
            Available time slots
          </div>
          <input
            className="field mt-4 mb-4"
            type="text"
            placeholder="First name"
            name="firstName"
            onChange={(e) => setData({ ...data, firstName: e.target.value })}
          ></input>
          <input
            className="field mt-4 mb-4"
            type="text"
            placeholder="Last name"
            name="lastName"
            onChange={(e) => setData({ ...data, lastName: e.target.value })}
          ></input>
          <input
            className="field mt-4 mb-4"
            type="date"
            placeholder="Date of birth (mm/dd/yyyy)"
            name="DOB"
            onChange={(e) => setData({ ...data, DOB: e.target.value })}
          ></input>
          <div className="field mb-4">
            <select
              style={{ border: "none", fontFamily: "montserrat-regular" }}
              id="inputState"
              className="form-control pl-0"
              name="gender"
              onChange={(e) => setData({ ...data, gender: e.target.value })}
            >
              <option defaultValue>Gender</option>
              <option>Male</option>
              <option>Female</option>
            </select>
          </div>
          <input
            className="field mt-4 mb-4"
            type="email"
            placeholder="Email address"
            name="email"
            onChange={(e) => setData({ ...data, email: e.target.value })}
          ></input>
          <input
            className="field mt-4 mb-4"
            type="text"
            placeholder="Phone number"
            name="phoneNumber"
            onChange={(e) => setData({ ...data, phoneNumber: e.target.value })}
          ></input>
          <input
            className="field mt-4 mb-4"
            type="text"
            placeholder="Address line 1"
            name="address1"
            onChange={(e) => setData({ ...data, address1: e.target.value })}
          ></input>
          <input
            className="field mt-4 mb-4"
            type="text"
            placeholder="Address line 2"
            name="address2"
            onChange={(e) => setData({ ...data, address2: e.target.value })}
          ></input>
          <div className="field mb-4">
            <select
              style={{ border: "none", fontFamily: "montserrat-regular" }}
              id="inputState"
              className="form-control pl-0"
              name="city"
              onChange={(e) => setData({ ...data, city: e.target.value })}
            >
              <option
                style={{ fontFamily: "montserrat-regular", fontSize: "14px" }}
                defaultValue
              >
                City
              </option>
              <option>1</option>
              <option>2</option>
            </select>
          </div>
          <div className="field mb-4">
            <select
              style={{ border: "none", fontFamily: "montserrat-regular" }}
              id="inputState"
              className="form-control pl-0"
              name="state"
              onChange={(e) => setData({ ...data, state: e.target.value })}
            >
              <option defaultValue>State</option>
              <option>Texas</option>
              <option>Florida</option>
              <option>Virginia</option>
            </select>
          </div>
          <div className="field mb-4">
            <select
              style={{ border: "none", fontFamily: "montserrat-regular" }}
              id="inputState"
              className="form-control pl-0"
              name="country"
              onChange={(e) => setData({ ...data, country: e.target.value })}
            >
              <option selected>Country</option>
              <option>United State</option>
              <option>Germany</option>
            </select>
          </div>
          <input
            className="field mt-4 mb-4"
            type="text"
            placeholder="Postal Code"
            name="postalCode"
            onChange={(e) => setData({ ...data, postalCode: e.target.value })}
          ></input>
        </div>
      </div>
      {/* <Link to="/confrim-appoint"> */}
      <div className="btn_start text-center" to="/login">
        <button onClick={() => handleUpdateData(data)} className="login_btn">
          Next
        </button>
      </div>
      {/* </Link> */}
    </>
  );
};

export default FullData;
